#LinksScreen {
    display: none;
}



.backArrow {
    height: 100%;
    width: 5%;
    margin: 0% 0% 0% 2.5%;
    /* background-color: green; */
    /* padding: 0%; */
}

#linksContainer {
    position: absolute;
    display: flex;
    justify-content: center;
    align-self: center;
    height: 35%;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    /* background-color: blue; */
}

#linksContainer svg {
    position: relative;
    /* border: rgb(212, 1, 114) 5px groove; */
}

#linksContainer svg:nth-child(odd) {
    top: -25%;
    /* padding: 1; */
}

#linksContainer svg:nth-child(even) {
    top: 25%;
    right: 3%;
}